@mixin border-color($border-radius: 1rem) {
    &::after {
        content: '';
        position: absolute;
        inset: -1px;
        z-index: -2;
        background: rgba(255, 255, 255, 0.1);
        backdrop-filter: blur(15px);
    }

    &::before {
        content: '';
        position: absolute;
        inset: 0;
        z-index: -1;
        border-radius: $border-radius;
        border: 1px solid transparent;
        background: linear-gradient(to right bottom, #624f86, #ffedd4, #5229a1) border-box;
        -webkit-mask: linear-gradient(#fff 0 0) padding-box, linear-gradient(#fff 0 0);
        -webkit-mask-composite: xor;
        mask-composite: exclude;
    }
}


@mixin button() {
    width: 37rem;
    height: 8.3rem;

    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 6rem;
    background: #FF5129;

    color: white !important;

    font-family: "Uni Neue", sans-serif;
    font-size: 3.2rem;
    font-style: normal;
    font-weight: 800;
    line-height: 110%;
    transition: .3s ease box-shadow;

    &:hover {
        -webkit-box-shadow: 0 0 30px 0 #ff5129, 0 0 5px 0 #ff5129;
        box-shadow: 0 0 30px 0 #ff5129, 0 0 5px 0 #ff5129;
    }

    @include breakpoint(large-desktop) {
        width: 30rem;
        height: 6.3rem;
        font-size: 2.6rem;
    }

    @include breakpoint(tablet) {
        font-size: 2.4rem;
        font-style: normal;
        font-weight: 800;
        line-height: 110%;
        padding: 2.4rem 5.6rem;
        width: fit-content;
        height: auto;
        margin: 0 auto;
        margin-top: 3.2rem;
    }
}